import React from 'react';
import './containers/index.css';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank';
import FaceRecog from './components/FaceRecog/FaceRecog';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import Signin from './components/signin/Signin';
import Register from './components/register/Register';
import Theme from './components/Theme/Theme';
import Profile from './components/Profile/Profile';

const app =new Clarifai.App(
  {
    apiKey: '407fc2dd43e3465facfde5a0cfa8c69b'
  }
);

//Shows Particles in background
const Particle= {
  particles: {
    number:{
      value:120,
      density: {
        enable:true,
        value_area:1550
      }
    },
    move:{
      enable:true,
      speed:12,
    }
  }
}

const initialState={
  input: '',
  imageURL : '',
  route: 'signin',
  isSignedIn:false,
  isTheme:'Dark',
  user:{
    id:'',
    name:'',
    email:'',
    entries:0,
    joined: '',
  }
}

class Home extends React.Component {
  constructor(){
    super();
    //Defines state of the app
    this.state= initialState
  }
  
  //Changes the Theme in the State according to which the color scheme will change
  DarkMode=()=>{
    if (this.state.isTheme==='Dark') {
      this.setState({isTheme:'Light'})
      //If the Button is Light then Dark theme is executed on click of button
      document.querySelector("html").style.background= "linear-gradient(89deg,#FF5EDF 10%, #04C8DE 100%)";
    } else {
      //If the Button is Dark then Light theme is executed on click of button
      this.setState({isTheme:'Dark'})
      document.querySelector("html").style.backgroundImage= "linear-gradient(to right, blue,red)"
    }
  }

  loadUser = (data)=>{
    this.setState({user:
      {
        id:data.id,
        name:data.name,
        email:data.email,
        entries:data.entries,
        joined: data.joined,
      }
    })
  }

  //Takes the input i.e. the URL
  onInput = (event) =>{
    this.setState({input: event.target.value});
  }
  //On the button press(submit) display the image with the face recognition
  onSubmit = ()=>{

    this.setState({imageURL:this.state.input})

    if (document.getElementById('ImageInput').value!=='') {
      document.getElementById('Face__info').innerHTML='';
      document.getElementById('ImageInput').value=""
        fetch('http://localhost:3000/image',{
          method: 'put',//Fetch's method is default "get" 
          //so it turns that into put
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          },
          body: JSON.stringify({
            id: this.state.user.id
          })
       })
       .then(response=>response.json())
       .then(count=>{
         this.setState(Object.assign(this.state.user,{entries:count}))
       })
       //CLARIFAI IS NOT WORKING
    }
  }
  //CHANGES THE ROUTE THUS CHANGING THE STRUCTURE OF APP
  onRouteChange = (rote)=>{
    if (rote=== 'signout'){
      this.setState(initialState)
    }
    else if(rote === 'home') {
      this.setState({isSignedIn: true})
    }
    this.setState({route:rote})
  }
  render(){
    const{imageURL,isSignedIn,route,isTheme} = this.state;

    //Sets the default theme as "DARK"
    if (this.state.isTheme==='Dark'){
      document.querySelector("html").style.backgroundImage= "linear-gradient(to right, blue,red)"
    }

    const {onRouteChange,onInput,onSubmit,loadUser,DarkMode} = this;

    return (
      <div className={`App`}>
        <Particles className="particles"
          params={Particle} />
        <Navigation  onRouteChange={onRouteChange} isSignedIn={isSignedIn}/>
        <Theme isTheme={isTheme} DarkMode={DarkMode} />
        {route === 'home'
        ?//Ternary operators ? means IF and : means ELSE
        <div>
          <Logo isTheme={isTheme}/>
          <Rank entries = {this.state.user.entries} name = {this.state.user.name}/>
          <ImageLinkForm onInput={onInput} isTheme={isTheme} onSubmit={onSubmit}/>
          <FaceRecog imageURL={imageURL}/>
        </div>
        : (
          route === 'signin'
          ?<Signin loadUser={loadUser} onRouteChange={onRouteChange}/>
          :(
            route === 'profile'
            ?<Profile name={this.state.user.name} onRouteChange={onRouteChange} email={this.state.user.email} joined={this.state.user.joined}/>
            :<Register onRouteChange={onRouteChange} loadUser={loadUser}/>
          )
        )
        }
      </div>
    );
  }
}
export default Home;
