import React from 'react';

class Profile extends React.Component{
    render(){
        const {name,email,joined,onRouteChange}= this.props;
        return(
            <div className="u-center-text b"> 
                <h1 className="mt6">Name: {name}</h1>
                <h1>Email: {email}</h1>
                <h1>Joined: {joined}</h1>
                <p className="f3 link dim pointer mt3 b--black ba" onClick={()=>onRouteChange('home')}>Back</p>
            </div>
        )
    }
}

export default Profile