import React from 'react';

const Theme = ({DarkMode,isTheme}) => {
    if(isTheme==='Light'){
        return(
            <nav className = "tp-right">
                <p onClick={DarkMode} className='f3 link dim black pa3 pointer underline'>Dark</p>
            </nav>
        );
    }
    else{
        return(
            <nav className = "tp-right">
                <p onClick={DarkMode} className='f3 link dim black pa3 pointer underline'>Light</p>
            </nav>
        )
    }
}
export default Theme